export interface RouterOptions {
  logger: LoggerService;
  config: Config;
}

export async function createRouter(
  options: RouterOptions,
): Promise<express.Router> {
  const { logger, config } = options;

  // const config = useApi(configApiRef);

  const router = Router();
  router.use(express.json());

  router.get('/health', (_, response) => {
    logger.info('PONG!');
    response.json({ status: 'ok' });
  });

  router.post('/incidents', (req, response) => {
    logger.info('incidents!');
    logger.info(config.get('ocm'));
    response.setHeader('Content-Type', 'application/json');
    logger.info('Body: ', req.body);

    /*
    let default_token = refresh(
      config.getString('backend.baseUrl'),
      config.getString('ocm.clientId'),
      config.getString('ocm.clientSecret'),
    ).catch((e) => {
      console.log("Error: ", e);
      response.status(500);
      response.json({error: e})
      return
    })
    .then((token) => {
      if (token.error) {
        response.status(400);
        response.json({error: token.error})
      } else {
        return token.access_token;
      }
    });
    */

    response.status(200);
    response.json({ status: 'not-implemented' });
    // response.json({ status: 'ok' });
  });
  router.use(errorHandler());
  return router;
}

